@import '~antd/dist/antd.less'; //  less 
html,
body {
  font-family: 'Tahoma For Number', 'Chinese Quote', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue',
    Helvetica, Arial, sans-serif;
  background-color: #fafafa;
  min-width: 960px;
}
.ant-progress-inner {
  box-shadow: 0 0 1px 0 #1890ff;
}

.detail {
  display: flex;
  padding: 20px 0;
  .aside {
    width: 200px;
    padding: 0 10px;
    .notes-catalog {
      max-height: 100vh;
      position: sticky;
      top: 0;
      overflow-y: auto;
    }
  }
  .detail-content {
    flex: 1 0;
  }
}
.ant-tree {
  background-color: transparent;
  &.ant-tree-directory .ant-tree-treenode-selected::before {
    background-color: transparent;
  }
  &.ant-tree-directory .ant-tree-treenode-selected:hover::before {
    background-color: #f5f5f5;
  }
  &.ant-tree-directory .ant-tree-treenode-selected .ant-tree-switcher,
  &.ant-tree-directory .ant-tree-treenode .ant-tree-node-content-wrapper.ant-tree-node-selected {
    color: #79a8a9;
  }
}

@primary-color: #79a8a9;@link-color: #79a8a9;@border-radius-base: 2px;