.user {
  overflow: hidden;
  .user-nav {
    float: left;
  }
  .user-content {
    float: left;
    width: calc(100% - 220px);
    padding: 10px;
    margin-left: 20px;
  }
  .ant-menu {
    background-color: transparent;
    border: none;
    padding: 0 10px;
    user-select: none;
    .ant-menu-item,
    .ant-menu-item-selected {
      border-radius: 4px;
      background-color: transparent;
      &:hover {
        background-color: #f5f5f5;
      }
      &::after {
        display: none;
      }
    }
    .ant-menu-item-selected {
      color: #79a8a9;
    }
    &:not(.ant-menu-horizontal) .ant-menu-item-selected {
      .ant-menu-item;
    }
  }
}

@primary-color: #79a8a9;@link-color: #79a8a9;@border-radius-base: 2px;